import { Button, Card, CardContent, CardHeader, Grid, TextField } from '@mui/material'
import { useEffect, useState } from 'react'
import ContainerPage from '../../../components/ContainerPage'
import PersonAddIcon from '@mui/icons-material/PersonAdd'
import { useLocation, useNavigate } from 'react-router-dom'
import { useGet } from '../../../customHooks/useGet'
import { usePut } from '../../../customHooks/usePut'

function EditUser() {
  const navigate = useNavigate()
  const user_id = useLocation().pathname.split('/')[2]
  const [user, setUser] = useState({ name: '', email: '', password: '', role: '',payment_account:'' })
  const { putData, isLoading, data: putD } = usePut()
  const { data, getData } = useGet()

  useEffect(() => {
    getData(`${process.env.REACT_APP_API}/api/users/${user_id}`)
  }, [])

  useEffect(() => {
    let timeOut
    if (putD.length > 0) {
      timeOut = setTimeout(() => {
        navigate('/users')
      }, 1500)
    }

    return () => clearTimeout(timeOut)
  }, [putD])

  useEffect(() => {
    if (data.length > 0) {
      setUser(...data.flat(Infinity))
    }
  }, [data])

  const handleUser = (e) => {
    e.preventDefault()

    putData(`${process.env.REACT_APP_API}/api/users/${user_id}`, JSON.stringify(user))
  }

  return (
    <ContainerPage title='Editar usuario'>
      <Grid item xs={12}>
        <Card>
          <CardHeader avatar={<PersonAddIcon color='disabled' />} />
          <CardContent>
            <Grid container spacing={1} component='form' onSubmit={handleUser}>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  name='name'
                  type='text'
                  label='Nombre'
                  value={user.name}
                  onChange={(e) => setUser({ ...user, [e.target.name]: e.target.value })}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  name='email'
                  type='email'
                  label='Email'
                  value={user.email}
                  onChange={(e) => setUser({ ...user, [e.target.name]: e.target.value })}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  required
                  name='payment_account'
                  type='text'
                  label='CBU/Alias'
                  onChange={(e) => setUser({ ...user, [e.target.name]: e.target.value })}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  name='password'
                  type='password'
                  value={user.password}
                  label='Contraseña'
                  onChange={(e) => setUser({ ...user, [e.target.name]: e.target.value })}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  name='role'
                  type='text'
                  label='Rol'
                  value={user.role}
                  onChange={(e) => setUser({ ...user, [e.target.name]: e.target.value })}
                />
              </Grid>
              <Grid item xs={12}>
                <Button disabled={isLoading} type='submit' variant='outlined' fullWidth color='secondary'>
                  {isLoading ? 'Guardando proveedor' : putD.length > 0 ? 'Guardado' : 'Guardar proveedor'}
                </Button>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Grid>
    </ContainerPage>
  )
}

export default EditUser
