import { Button, Card, CardActions, CardContent, CardHeader, Grid, TextField } from '@mui/material'
import { useEffect, useState } from 'react'
import ContainerPage from '../../../components/ContainerPage'
import { usePost } from '../../../customHooks/usePost'
import DomainAddIcon from '@mui/icons-material/DomainAdd'
import { useNavigate } from 'react-router-dom'

function AddProvider() {
  const navigate = useNavigate()
  const [provider, setProvider] = useState({})
  const { isLoading, postData, data } = usePost()

  useEffect(() => {
    let timeOut
    if (data.length > 0) {
      timeOut = setTimeout(() => {
        navigate('/providers')
      }, 1500)
    }

    return () => clearTimeout(timeOut)
  }, [data])

  const handleProvider = (e) => {
    e.preventDefault()
    postData(`${process.env.REACT_APP_API}/api/providers`, JSON.stringify(provider))
  }

  return (
    <ContainerPage title={'Nuevo proveedor'}>
      <Grid item xs={12}>
        <form onSubmit={handleProvider}>
          <Card>
            <CardHeader avatar={<DomainAddIcon color='disabled' />} />
            <CardContent>
              <Grid container spacing={1}>
                <Grid item xs={12} p={0}>
                  <TextField
                    label='Nombre de proveedor'
                    fullWidth
                    name='name'
                    onChange={(e) => setProvider({ ...provider, [e.target.name]: e.target.value })}
                  />
                </Grid>
                <Grid item xs={12} p={0}>
                  <TextField
                    label='Telefono'
                    fullWidth
                    name='phone'
                    onChange={(e) => setProvider({ ...provider, [e.target.name]: e.target.value })}
                  />
                </Grid>
              </Grid>
            </CardContent>
            <CardActions>
              <Grid container>
                <Grid item xs={12}>
                  <Button  disabled={isLoading} type='submit' variant='outlined' fullWidth color='secondary'>
                    {isLoading ? 'Guardando proveedor' : data.length > 0 ? 'Guardado' : 'Guardar proveedor'}
                  </Button>
                </Grid>
              </Grid>
            </CardActions>
          </Card>
        </form>
      </Grid>
    </ContainerPage>
  )
}

export default AddProvider
