import { Button, Card, CardContent, CardHeader, Grid, TextField } from '@mui/material'
import { useEffect, useState } from 'react'
import Box from '@mui/material/Box'
import InputLabel from '@mui/material/InputLabel'
import MenuItem from '@mui/material/MenuItem'
import FormControl from '@mui/material/FormControl'
import Select from '@mui/material/Select'
import { useGet } from '../../../customHooks/useGet'
import ContainerPage from '../../../components/ContainerPage'
import { usePost } from '../../../customHooks/usePost'
import ArticleIcon from '@mui/icons-material/Article'
import { useNavigate } from 'react-router-dom'
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers'
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFnsV3'

function ProviderSelect({ bill, setBill }) {
  const { data, getData } = useGet()

  useEffect(() => {
    getData(`${process.env.REACT_APP_API}/api/providers`)
  }, [])

  return (
    <Box sx={{ minWidth: 120 }}>
      {data && (
        <FormControl fullWidth>
          <InputLabel id='demo-simple-select-label'>Proveedor</InputLabel>
          <Select
            labelId='demo-simple-select-label'
            id='demo-simple-select'
            value={bill.provider_id}
            label='Proveedor'
            name='provider_id'
            onChange={(e) => setBill({ ...bill, [e.target.name]: e.target.value })}
          >
            {data.flat(Infinity).map((i) => (
              <MenuItem key={i._id} value={i._id}>
                {i.name}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      )}
    </Box>
  )
}

function AddBill() {
  const [bill, setBill] = useState({ provider_id: '' })
  const [bill_date, setBillDate] = useState(new Date())
  const navigate = useNavigate()

  const { postData, isLoading, data } = usePost()

  useEffect(() => {
    let timeOut
    if (data.length > 0) {
      timeOut = setTimeout(() => {
        navigate('/bills')
      }, 1500)
    }

    return () => clearTimeout(timeOut)
  }, [data])

  const handleProvider = (e) => {
    e.preventDefault()

    postData(`${process.env.REACT_APP_API}/api/bills`, JSON.stringify({ ...bill, bill_date:bill_date}))
  }

  return (
    <form onSubmit={handleProvider}>
      <ContainerPage title='Nueva factura' actions={<></>}>
        <Grid item xs={12}>
          <Card variant='outlined'>
            <CardHeader avatar={<ArticleIcon color='disabled' />} />
            <CardContent>
              <Grid container spacing={1}>
                <Grid item xs={6}>
                  <ProviderSelect bill={bill} setBill={setBill} />
                </Grid>
                <Grid item xs={6}>
                  <LocalizationProvider dateAdapter={AdapterDateFns}>
                    <DatePicker label='Fecha de fac.' value={bill_date} onChange={(e) => setBillDate(e)} />
                  </LocalizationProvider>
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    name='bill_code'
                    label='Codigo de factura'
                    onChange={(e) => setBill({ ...bill, [e.target.name]: e.target.value })}
                  />
                </Grid>
                <Grid item xs={12}>
                  <Button disabled={isLoading} color='secondary' variant='outlined' fullWidth type='submit'>
                    {isLoading ? 'Guardando' : data.length > 0 ? 'Guardado' : 'Guardar'}
                  </Button>
                </Grid>
              </Grid>
            </CardContent>
          </Card>
        </Grid>
      </ContainerPage>
    </form>
  )
}

export default AddBill
