import { Avatar, Card, CardContent, CardHeader, Divider, Grid, IconButton, Typography } from '@mui/material'
import { Fragment, useEffect, useState } from 'react'
import { useGet } from '../../customHooks/useGet'
import ContainerPage from '../../components/ContainerPage'
import UserSelect from '../../components/UsersSelect'
import BusinessCenterIcon from '@mui/icons-material/BusinessCenter'
import { useNavigate } from 'react-router-dom'

function ConsigmentsPage() {
  const [selectedUser, setSelectedUser] = useState('')
  const navigate = useNavigate()
  const { data, isLoading, error, getData, setData } = useGet()

  useEffect(() => {
    if (selectedUser) {
      setData([])
      getData(`${process.env.REACT_APP_API}/api/consigments/${selectedUser}`)
    }
  }, [selectedUser])

  return (
    <ContainerPage
      title='Consignaciones'
      actions={<IconButton onClick={() => navigate('/consigments/add')}>+</IconButton>}
    >
      <Grid item xs={12}>
        <Card variant='outlined'>
          <CardHeader avatar={<BusinessCenterIcon color='disabled' />} />
          <CardContent>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <UserSelect userId={selectedUser} setUserId={setSelectedUser} />
              </Grid>
              {data.flat(Infinity).map((i, j) => (
                <Fragment key={j}>
                  <Grid item xs={12}>
                    <Divider />
                  </Grid>
                  <Grid item xs={2}>
                    <Avatar src='' variant='rounded' />
                  </Grid>
                  <Grid item xs={8}>
                    <span>{`${i.name}`}</span>
                    <p>{`${i.product_code}`}</p>
                  </Grid>
                  <Grid item xs={1}>
                    <Typography textAlign='end'>{i.quantity}</Typography>
                  </Grid>
                  <Grid item xs={12}>
                    <Divider />
                  </Grid>
                </Fragment>
              ))}
            </Grid>
          </CardContent>
        </Card>
      </Grid>
    </ContainerPage>
  )
}

export default ConsigmentsPage
