import { Card, Grid, IconButton, CardContent, Divider, CardHeader, Typography } from '@mui/material'
import { Fragment, useEffect } from 'react'
import { useGet } from '../../customHooks/useGet'
import { Add } from '@mui/icons-material'
import { useNavigate } from 'react-router-dom'
import ContainerPage from '../../components/ContainerPage'
import PercentIcon from '@mui/icons-material/Percent';

function PercentagePage() {
  const navigate = useNavigate()

  const { data, getData } = useGet()

  useEffect(() => {
    getData(`${process.env.REACT_APP_API}/api/percentage`)
  }, [])

  const handleGetBillById = (bill_code, provider_id) => {
    // navigate(`/percentage/add`)
  }

  return (
    <ContainerPage
      title={'Porcentajes'}
      actions={
        <div>
          <IconButton
            onClick={() => {
              navigate(`/percentages/add`)
            }}
          >
            <Add />
          </IconButton>

        </div>
      }
    >
      <Grid item xs={12}>
        <Card variant='outlined'>
          <CardHeader avatar={<PercentIcon color='disabled' />} />
          <CardContent>
            <Grid container>
              <Grid item xs={12} mt={3}>
                <Divider />
              </Grid>
              <Grid item xs={4} p={1}>
                Fecha
              </Grid>
              <Grid item xs={4} p={1}>
                Tipo
              </Grid>
              <Grid item xs={4} p={1} textAlign='end'>
                Porcentaje
              </Grid>
              <Grid item xs={12}>
                <Divider />
              </Grid>
              {data &&
                data.length > 0 &&
                data.flat(Infinity).map((i) => (
                  <Fragment key={i._id}>
                    <Grid item xs={4} p={1}>
                      <Typography noWrap fontSize='12px' component='p'>
                        {new Date(i.createdAt).toLocaleDateString()}
                      </Typography>
                    </Grid>
                    <Grid item xs={4} p={1}>
                      <Typography noWrap fontSize='12px' component='p'>
                        {i.type}
                      </Typography>
                    </Grid>
                    <Grid
                      item
                      xs={4}
                      p={1}
                      textAlign='end'
                      onClick={() => handleGetBillById(i.bill_code, i.provider_id)}
                    >
                      <Typography noWrap fontSize='12px' component='p'>
                        {i.percentage} %
                      </Typography>
                    </Grid>
                    <Grid item xs={12}>
                      <Divider />
                    </Grid>
                  </Fragment>
                ))}
            </Grid>
          </CardContent>
        </Card>
      </Grid>
    </ContainerPage>
  )
}

export default PercentagePage
