import {
  Button,
  Card,
  CardContent,
  CardHeader,
  Grid,
  TextField,
  FormGroup,
  FormControlLabel,
  Switch,
} from '@mui/material'
import { useEffect, useState } from 'react'
import ContainerPage from '../../../components/ContainerPage'
import PersonAddIcon from '@mui/icons-material/PersonAdd'
import { usePost } from '../../../customHooks/usePost'
import { useNavigate } from 'react-router-dom'

function AddUser() {
  const navigate = useNavigate()
  const [user, setUser] = useState({
    name: '',
    email: '',
    password: '',
    role: '',
    isActive: true,
    isAdmin: false,
    payment_account: '',
  })
  const { postData, isLoading, data } = usePost()

  useEffect(() => {
    let timeOut
    if (data.length > 0) {
      timeOut = setTimeout(() => {
        navigate('/users')
      }, 1500)
    }
    return () => clearTimeout(timeOut)
  }, [data])

  const handleUser = (e) => {
    e.preventDefault()
    postData(`${process.env.REACT_APP_API}/api/users`, JSON.stringify(user))
  }

  const handleIsAdmin = (e) => {
    setUser({ ...user, isAdmin: e.target.checked })
  }
  const handleIsActive = (e) => {
    setUser({ ...user, isActive: e.target.checked })
  }
  return (
    <ContainerPage title='Nuevo usuario'>
      <Grid item xs={12}>
        <Card>
          <CardHeader avatar={<PersonAddIcon color='disabled' />} />
          <CardContent>
            <Grid container spacing={1} component='form' onSubmit={handleUser}>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  required
                  name='name'
                  type='text'
                  label='Nombre'
                  onChange={(e) => setUser({ ...user, [e.target.name]: e.target.value })}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  required
                  name='email'
                  type='email'
                  label='Email'
                  onChange={(e) => setUser({ ...user, [e.target.name]: e.target.value })}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  required
                  name='payment_account'
                  type='text'
                  label='CBU/Alias'
                  onChange={(e) => setUser({ ...user, [e.target.name]: e.target.value })}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  required
                  name='password'
                  type='password'
                  label='Contraseña'
                  onChange={(e) => setUser({ ...user, [e.target.name]: e.target.value })}
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <FormGroup>
                  <FormControlLabel
                    control={<Switch color='secondary' onChange={handleIsAdmin} checked={user.isAdmin} />}
                    label='Administrador'
                  />
                </FormGroup>
              </Grid>
              <Grid item xs={12} md={6}>
                <FormGroup>
                  <FormControlLabel
                    control={<Switch color='secondary' onChange={handleIsActive} checked={user.isActive} />}
                    label='Activar'
                  />
                </FormGroup>
              </Grid>

              <Grid item xs={12}>
                <Button disabled={isLoading} type='submit' variant='outlined' fullWidth color='secondary'>
                  {isLoading ? 'Guardando usuario' : data.length > 0 ? 'Guardado' : 'Guardar usuario'}
                </Button>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Grid>
    </ContainerPage>
  )
}

export default AddUser
