import { Card, Grid, IconButton, CardContent, Divider, CardHeader, Typography } from '@mui/material'
import { Fragment, useEffect } from 'react'
import { useGet } from '../../customHooks/useGet'
import { Add } from '@mui/icons-material'
import RequestQuoteIcon from '@mui/icons-material/RequestQuote'
import { useNavigate } from 'react-router-dom'
import { formatNumber } from '../../commons'
import ContainerPage from '../../components/ContainerPage'
import ListAltIcon from '@mui/icons-material/ListAlt'

function BillsPage() {
  const navigate = useNavigate()

  const { data, getData } = useGet()

  useEffect(() => {
    getData(`${process.env.REACT_APP_API}/api/bills`)
  }, [])

  const handleGetBillById = (bill_code, provider_id) => {
    navigate(`/bills/${bill_code}/${provider_id}`)
  }

  return (
    <ContainerPage
      title={'Facturas'}
      actions={
        <div>
          <IconButton
            onClick={() => {
              navigate(`/bills/add-expense`)
            }}
          >
            <RequestQuoteIcon />
          </IconButton>
          <IconButton
            onClick={() => {
              navigate(`/bills/add-bill`)
            }}
          >
            <Add />
          </IconButton>

          
        </div>
      }
    >
      <Grid item xs={12}>
        <Card variant='outlined'>
          <CardHeader avatar={<ListAltIcon color='disabled' />} />
          <CardContent>
            <Grid container>
              <Grid item xs={12} mt={3}>
                <Divider />
              </Grid>
              <Grid item xs={4} p={1}>
                Fecha
              </Grid>
              <Grid item xs={4} p={1}>
                Proveedor
              </Grid>
              <Grid item xs={4} p={1} textAlign='end'>
                Total
              </Grid>
              <Grid item xs={12}>
                <Divider />
              </Grid>
              {data &&
                data.length > 0 &&
                data.flat(Infinity).map((i) => (
                  <Fragment key={i._id}>
                    <Grid item xs={4} p={1}>
                      <Typography noWrap fontSize='12px' component='p'>
                        {new Date(i.bill_date).toLocaleDateString()}
                      </Typography>
                    </Grid>
                    <Grid item xs={4} p={1}>
                      <Typography noWrap fontSize='12px' component='p'>
                        {i.providerName}
                      </Typography>
                    </Grid>
                    <Grid
                      item
                      xs={4}
                      p={1}
                      textAlign='end'
                      onClick={() => handleGetBillById(i.bill_code, i.provider_id)}
                    >
                      <Typography noWrap fontSize='12px' component='p'>
                        {formatNumber(i?.total || 0)}
                      </Typography>
                    </Grid>
                    <Grid item xs={12}>
                      <Divider />
                    </Grid>
                  </Fragment>
                ))}
            </Grid>
          </CardContent>
        </Card>
      </Grid>
    </ContainerPage>
  )
}

export default BillsPage
