import {  Button, Grid, TextField } from '@mui/material'
import { useContext, useEffect, useState } from 'react'
import { SocketContext } from '../../contexts/socketContext'
import Box from '@mui/material/Box'
import InputLabel from '@mui/material/InputLabel'
import MenuItem from '@mui/material/MenuItem'
import FormControl from '@mui/material/FormControl'
import Select from '@mui/material/Select'

function ProductSelect({ product, setProduct }) {
    const { socket } = useContext(SocketContext)
    const [products, setProducts] = useState()
  
    useEffect(() => {
      socket.on('products', (payload) => {
        setProducts(payload)
      })
    }, [socket])
  
    return (
      <Box sx={{ minWidth: 120 }}>
        {products && (
          <FormControl fullWidth>
            <InputLabel id='demo-simple-select-label'>Producto</InputLabel>
            <Select
              labelId='demo-simple-select-label'
              id='demo-simple-select'
              value={product.product_code}
              label='Producto'
              name='product_code'
              onChange={(e) => setProduct({ ...product, [e.target.name]: e.target.value })}
            >
              {products.map((i) => (
                <MenuItem key={i._id} value={i.product_code}>
                  {i.name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        )}
      </Box>
    )
  }

function ChangeUnitPrice() {
  
  const { socket } = useContext(SocketContext)
  const [product, setProduct] = useState([])
  useEffect(() => {

    // socket.emit('save_consigment', consigment)
  }, [socket])


  const handleChangeUnitPrice = (e) => {
    e.preventDefault()
  
  }
  return (
    <div className='ChangeUnitPrice'>
      <Grid container component='form' onSubmit={handleChangeUnitPrice}>
        <Grid item xs={12}>
       < ProductSelect  product={product} setProduct={setProduct}/>
       </Grid>
       <Grid item xs={12}>
       <TextField
              fullWidth
              name='unit_price'
              label='Precio unitario'
              onChange={(e) => setProduct({ ...product, [e.target.name]: e.target.value })}
            />
       </Grid>
       <Button type='submit'>Guardar precio</Button>
      </Grid>
    </div>
  )
}

export default ChangeUnitPrice
