import { Button, Card, CardActions, CardContent, CardHeader, Divider, Grid, Typography } from '@mui/material'
import { useContext, useEffect } from 'react'
import { useGet } from '../../../customHooks/useGet'
import { usePost } from '../../../customHooks/usePost'
import ContainerPage from '../../../components/ContainerPage'
import { formatNumber } from '../../../commons'
import { AppContext } from '../../../contexts/AppContext'
import { useLocation } from 'react-router-dom'
import HourglassEmptyIcon from '@mui/icons-material/HourglassEmpty'
import CheckBoxIcon from '@mui/icons-material/CheckBox'

function SellDetail() {
  const { userInfo } = useContext(AppContext)
  const sell_code = useLocation().pathname.split('/')[2]
  const { data, getData } = useGet()
  const { postData } = usePost()

  useEffect(() => {
    if (userInfo) {
      getData(`${process.env.REACT_APP_API}/api/transactions/${sell_code}`)
    }
  }, [userInfo])

  const handleComissionPayment = (e) => {
    e.preventDefault()

    postData(`${process.env.REACT_APP_API}/api/transactions/comission`, JSON.stringify({ sell_code }))
  }

  return (
    <ContainerPage title='Detalle'>
      {data.flat(Infinity).map((i, j) => (
        <Grid item xs={12} key={j}>
          <Grid container spacing={1}>
            <Grid item xs={12}>
              <Card component='form' onSubmit={handleComissionPayment}>
                <CardHeader
                  title={'Transaccion'}
                  subheader={i.transaction_code}
                  action={
                    i.totalComissionNotLiquidated === 0 ? (
                      <CheckBoxIcon color='success' />
                    ) : (
                      <HourglassEmptyIcon color='action' />
                    )
                  }
                />
                <CardContent>
                  <Grid container>
                    <Grid item textAlign='center' xs={4}>
                      <p>Total </p>
                      {formatNumber(i?.total_sales_amount | 0)}
                    </Grid>
                    <Grid item textAlign='center' xs={4}>
                      <p>Items</p>
                      {i.total_quantity}
                    </Grid>
                    <Grid item textAlign='center' xs={4}>
                      <p>Comision</p>
                      {formatNumber(i?.total_commission | 0)}
                    </Grid>
                  </Grid>
                </CardContent>
                {userInfo?.role === 'administrator' && (
                  <CardActions>
                    <Button disabled={i.totalComissionNotLiquidated === 0} type='submit' fullWidth variant='outlined'>
                      Pagar Comision
                    </Button>
                  </CardActions>
                )}
              </Card>
            </Grid>
            <Grid item xs={12}>
              <Card>
                <CardHeader title='Productos' />
                <CardContent>
                  <Grid container>
                    {i.products.map((j, k) => (
                      <Grid item xs={12} key={k} p={1}>
                        <Grid container>
                          <Grid item xs={11}>
                            <Typography component='span' fontSize='13px'>
                              {j.name}
                            </Typography>
                          </Grid>
                          <Grid item xs={1}>
                            <Typography component='span' fontSize='13px'>
                              {j.quantity}
                            </Typography>
                          </Grid>
                          <Grid item xs={12}>
                            <Typography component='span' fontSize='13px'>
                              {formatNumber(j?.total_price | 0)}
                            </Typography>
                          </Grid>
                        </Grid>
                        <Divider />
                      </Grid>
                    ))}
                  </Grid>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Grid>
      ))}
      <Divider variant='fullWidth' />
    </ContainerPage>
  )
}

export default SellDetail
