import List from '@mui/material/List'
import ListItem from '@mui/material/ListItem'
import Divider from '@mui/material/Divider'
import ListItemText from '@mui/material/ListItemText'
import ListItemAvatar from '@mui/material/ListItemAvatar'
import Avatar from '@mui/material/Avatar'
import Typography from '@mui/material/Typography'
import { useGet } from '../../../customHooks/useGet'
import { Fragment, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'

export default function UserList() {
  const { data, getData } = useGet()
  const navigate = useNavigate()

  useEffect(() => {
    getData(`${process.env.REACT_APP_API}/api/users`)
  }, [])

  return (
    <List sx={{ width: '100%', bgcolor: 'background.paper' }}>
      {data.flat(Infinity).map((i) => (
        <Fragment key={i._id}>
          <ListItem
            alignItems='flex-start'
            onClick={() => {
              navigate(`/users/${i._id}`)
            }}
          >
            <ListItemAvatar>
              <Avatar alt={i.name} src={`${process.env.REACT_APP_API}/${i.avatar}`} />
            </ListItemAvatar>
            <ListItemText
              primary={i.name}
              secondary={
                <Fragment>
                  <Typography sx={{ display: 'inline' }} component='span' variant='body2' color='text.primary'>
                    {i.role}
                  </Typography>
                  {` — ${i.status === 'active' ? 'Activo ' : 'Inactivo '}`}
                  <Typography component='span'>{i.email}</Typography>
                </Fragment>
              }
            />
          </ListItem>
          <Divider variant='inset' component='li' />
        </Fragment>
      ))}
    </List>
  )
}
