import { Button, Card, CardContent, CardHeader, Grid, TextField } from '@mui/material'
import { useEffect, useState } from 'react'
import ContainerPage from '../../../components/ContainerPage'
import { usePost } from '../../../customHooks/usePost'
import MonetizationOnIcon from '@mui/icons-material/MonetizationOn'
import { useNavigate } from 'react-router-dom'
import SelectPaymentMethod from '../../../components/SelectPaymentMethod'

function AddExpense() {
  const [expense, setExpense] = useState({})
  const navigate = useNavigate()
  const [paymentMethod, setPaymentMethod] = useState('Efectivo')

  const { postData, data, isLoading } = usePost()

  useEffect(() => {
    let timeOut
    if (data.length > 0) {
      timeOut = setTimeout(() => {
        navigate('/bills')
      }, 1500)
    }

    return () => clearTimeout(timeOut)
  }, [data])
  
  const handleProvider = (e) => {
    e.preventDefault()
    postData(`${process.env.REACT_APP_API}/api/transactions/expense`, JSON.stringify({ ...expense, paymentMethod }))
  }

  return (
    <form onSubmit={handleProvider}>
      <ContainerPage title='Nuevo gasto' actions={<></>}>
        <Grid item xs={12}>
          <Card variant='outlined'>
            <CardHeader avatar={<MonetizationOnIcon color='disabled' />} />
            <CardContent>
              <Grid container spacing={1}>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    name='amount'
                    label='Monto'
                    onChange={(e) => setExpense({ ...expense, [e.target.name]: e.target.value })}
                  />
                </Grid>{' '}
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    name='description'
                    label='Descripcion'
                    onChange={(e) => setExpense({ ...expense, [e.target.name]: e.target.value })}
                  />
                </Grid>
                <SelectPaymentMethod paymentMethod={paymentMethod} setPaymentMethod={setPaymentMethod} />
                <Grid item xs={12}>
                  <Button disabled={isLoading} color='secondary' variant='outlined' fullWidth type='submit'>
                    {isLoading ? 'Guardando' : data.length > 0 ? 'Guardado' : 'Guardar'}
                  </Button>
                </Grid>
              </Grid>{' '}
              {/**/}
            </CardContent>
          </Card>
        </Grid>
      </ContainerPage>
    </form>
  )
}

export default AddExpense
