import { Button, Card, CardActions, CardContent, CardHeader, Grid, TextField } from '@mui/material'
import { useContext, useEffect, useState } from 'react'
import ContainerPage from '../../components/ContainerPage'
import AccountCircleIcon from '@mui/icons-material/AccountCircle'
import { AppContext } from '../../contexts/AppContext'
import { usePut } from '../../customHooks/usePut'
import parseJwt from '../../middlewares'

function ProfilePage() {
  const { userInfo } = useContext(AppContext)
  const { data, putData, isLoading } = usePut()
  const [profileInfo, setProfileInfo] = useState(userInfo)

  useEffect(() => {
    if (data.length > 0) {
      const decoded = parseJwt(data[0].token)

      const userSession = {
        ...decoded,
        token: data[0].token,
      }

      localStorage.setItem('setFMI', JSON.stringify(data[0].token))

      setProfileInfo(userSession)
    }
  }, [data])

  const updateUserProfile = (e) => {
    e.preventDefault()
    putData(`${process.env.REACT_APP_API}/api/users/profile/${userInfo._id}`, JSON.stringify(profileInfo))
  }

  return (
    <ContainerPage title={'Perfil'}>
      <Grid item xs={12} component='form' onSubmit={updateUserProfile}>
        <Card>
          <CardHeader subheader={profileInfo.role} avatar={<AccountCircleIcon color='disabled' />} />
          <CardContent>
            <Grid container spacing={1}>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  onChange={(e) => setProfileInfo({ ...profileInfo, [e.target.name]: e.target.value })}
                  name='name'
                  label='Nombre'
                  value={profileInfo.name}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  onChange={(e) => setProfileInfo({ ...profileInfo, [e.target.name]: e.target.value })}
                  name='lastName'
                  label='Apellido'
                  value={profileInfo?.lastName || ''}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  onChange={(e) => setProfileInfo({ ...profileInfo, [e.target.name]: e.target.value })}
                  name='email'
                  label='Email'
                  value={profileInfo.email}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  onChange={(e) => setProfileInfo({ ...profileInfo, [e.target.name]: e.target.value })}
                  name='payment_account'
                  label='CBU/Alias'
                  value={profileInfo.payment_account}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  onChange={(e) => setProfileInfo({ ...profileInfo, [e.target.name]: e.target.value })}
                  name='password'
                  label='Contrasena'
                  value={profileInfo.password}
                />
              </Grid>
            </Grid>
          </CardContent>
          <CardActions>
            <Grid container spacing={1}>
              <Grid item xs={12}>
                <Button variant='outlined' disabled={isLoading} fullWidth type='submit'>
                  Actualizar
                </Button>
              </Grid>
            </Grid>
          </CardActions>
        </Card>
      </Grid>
    </ContainerPage>
  )
}

export default ProfilePage
