import { Button, Card, CardContent, CardHeader, Grid, TextField } from '@mui/material'
import { useEffect, useState } from 'react'
import ContainerPage from '../../../../components/ContainerPage'
import { usePost } from '../../../../customHooks/usePost'
import { useNavigate } from 'react-router-dom'
import { Percent } from '@mui/icons-material'

function AddPercentage() {
  const [percentage, setPercentage] = useState({ type: '', percentage: '' ,description: '' })
  const navigate = useNavigate()

  const { postData, isLoading, data } = usePost()

  useEffect(() => {
    let timeOut
    if (data.length > 0) {
      timeOut = setTimeout(() => {
        navigate('/percentage')
      }, 1500)
    }

    return () => clearTimeout(timeOut)
  }, [data])

  const handleProvider = (e) => {
    e.preventDefault()

    postData(`${process.env.REACT_APP_API}/api/percentage`, JSON.stringify({ ...percentage}))
  }

  return (
    <form onSubmit={handleProvider}>
      <ContainerPage title='Nuevo porcentaje' actions={<></>}>
        <Grid item xs={12}>
          <Card variant='outlined'>
            <CardHeader avatar={<Percent color='disabled' />} />
            <CardContent>
              <Grid container spacing={1}>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    name='type'
                    label='Tipo'
                    onChange={(e) => setPercentage({ ...percentage, [e.target.name]: e.target.value })}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    name='percentage'
                    label='Porcentaje'
                    type='number'
                    onChange={(e) => setPercentage({ ...percentage, [e.target.name]: e.target.value })}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    multiline
                    rows={5}
                    
                    name='description'
                    label='Descripcion'
                    onChange={(e) => setPercentage({ ...percentage, [e.target.name]: e.target.value })}
                  />
                </Grid>
                <Grid item xs={12}>
                  <Button disabled={isLoading} color='secondary' variant='outlined' fullWidth type='submit'>
                    {isLoading ? 'Guardando' : data.length > 0 ? 'Guardado' : 'Guardar'}
                  </Button>
                </Grid>
              </Grid>
            </CardContent>
          </Card>
        </Grid>
      </ContainerPage>
    </form>
  )
}

export default AddPercentage
