import { Avatar, Button, Card, CardContent, Grid, IconButton, Typography } from '@mui/material'
import { useEffect, useState } from 'react'
import Box from '@mui/material/Box'
import InputLabel from '@mui/material/InputLabel'
import MenuItem from '@mui/material/MenuItem'
import FormControl from '@mui/material/FormControl'
import Select from '@mui/material/Select'
import { useGet } from '../../../customHooks/useGet'
import { usePost } from '../../../customHooks/usePost'
import ContainerPage from '../../../components/ContainerPage'
import SearchProduct from '../../../components/SearchProduct'

function UserSelect({ userId, setUserId, setConsignedProducts }) {
  const { data, getData } = useGet()

  useEffect(() => {
    getData(`${process.env.REACT_APP_API}/api/users`)
  }, [])

  return (
    <Box sx={{ minWidth: 120 }}>
      {data.length > 0 && (
        <FormControl fullWidth>
          <InputLabel id='demo-simple-select-label'>Vendedor</InputLabel>
          <Select
            labelId='demo-simple-select-label'
            id='demo-simple-select'
            value={userId}
            label='Vendedor'
            name='employee'
            onChange={(e) => {
              e.preventDefault()
              setConsignedProducts([])
              setUserId(e.target.value)
              return e.target.value
            }}
          >
            {data.flat(Infinity).map((i) => (
              <MenuItem key={i._id} value={i._id}>
                {i.name}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      )}{' '}
      {/* */}
    </Box>
  )
}

function RegisterConsigment() {
  const [userId, setUserId] = useState('')
  const [consignedProducts, setConsignedProducts] = useState([])
  const [filteredproducts, setFilteredProducts] = useState([])

  const { data, getData, setData } = useGet()
  const { postData } = usePost()

  useEffect(() => {
    if (userId) {
      setData([])
      getData(`${process.env.REACT_APP_API}/api/products/products-by-consigments/${userId}`)
    }
  }, [userId])

  useEffect(() => {
    setConsignedProducts(data?.flat(Infinity))
  }, [data])

  const asignProduct = (product) => {
    let c = consignedProducts.map((i) => {
      if (i.product_code === product.product_code) {
        i.total_consigned_quantity = i.total_consigned_quantity + 1
        i.total_inventory_quantity = i.total_inventory_quantity - 1
        i.updated = true
      }
      return i
    })
    setConsignedProducts((prev) => c)
  }

  const desAsignProduct = (product) => {
    let c = consignedProducts.map((i) => {
      if (i.product_code === product.product_code) {
        i.total_consigned_quantity = i.total_consigned_quantity - 1
        i.total_inventory_quantity = i.total_inventory_quantity + 1
        i.updated = true
      }
      return i
    })
    setConsignedProducts((prev) => c)
  }

  const handleConsigment = (e) => {
   
    e.preventDefault()
   
    let products = consignedProducts
      .filter((j) => j.updated)
      .map((i) => ({
        product_code: i.product_code,
        total_consigned_quantity: i.total_consigned_quantity,
        updated: Boolean(i.updated),
      }))

    postData(`${process.env.REACT_APP_API}/api/consigments`, JSON.stringify({ data: products, employee: userId }))
  }

  return (
    <ContainerPage title='Registrar consignacion'>
      <Grid item xs={12}>
        <Card variant='outlined'>
          <CardContent>
            <Grid container>
              <Grid item xs={12} >
                <UserSelect userId={userId} setUserId={setUserId} setConsignedProducts={setConsignedProducts} />
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Grid>
      <Grid item xs={12}>
        {userId && (
          <>
            <SearchProduct data={consignedProducts} setFilteredProducts={setFilteredProducts}>
              <Box sx={{ minWidth: 120 }}>
                {consignedProducts &&
                  filteredproducts.flat(Infinity).map((i) => (
                    <div key={i.product_code} value={i.product_code} style={{ padding: '10px 0 10px 0' }}>
                      <Grid container>
                        <Grid item xs={2} style={{ display: 'flex', alignItems: 'center' }}>
                          <Avatar variant='rounded' style={{ height: '100%', width: '100%' }} src={i.image} />
                        </Grid>
                        <Grid
                          item
                          xs={10}
                          p={1}
                          style={{
                            backgroundColor: '#F4F6F6',
                            borderTopRightRadius: '10px',
                            borderBottomRightRadius: '10px',
                          }}
                        >
                          <Grid container>
                            <Grid item xs={10} display='flex' justifyContent='space-between'>
                              <Typography variant='subtitle2' fontSize='12px'>
                                {i.name}
                              </Typography>
                            </Grid>
                            <Grid item xs={2} display='flex' justifyContent='space-between'>
                              <Typography color='green' variant='p' fontSize='12px'>
                                {i.total_inventory_quantity}
                              </Typography>
                              <Typography variant='p' fontSize='12px'>
                                {i.total_consigned_quantity > 0 ? i.total_consigned_quantity : 0}
                              </Typography>
                            </Grid>
                            <Grid item>
                              <Typography variant='p' fontSize='12px'>
                                {i.product_code}
                              </Typography>
                            </Grid>
                          </Grid>
                          <Grid item xs={12} textAlign='end'>
                            <IconButton disabled={i.total_inventory_quantity < 1} onClick={() => asignProduct(i)}>
                              +
                            </IconButton>
                            <IconButton
                              disabled={!i.total_consigned_quantity || i.total_consigned_quantity < 1}
                              onClick={() => desAsignProduct(i)}
                            >
                              -
                            </IconButton>
                          </Grid>
                        </Grid>
                      </Grid>
                    </div>
                  ))}
              </Box>
            </SearchProduct>
            {consignedProducts.length > 0 && (
              <Button fullWidth onClick={handleConsigment} color='secondary' variant='outlined'>
                Guardar consignacion
              </Button>
            )}
          </>
        )}
      </Grid>
    </ContainerPage>
  )
}

export default RegisterConsigment
