import { Card, CardContent, CardHeader, Divider, Grid, IconButton, Typography } from '@mui/material'
import { Fragment, useContext, useEffect, useState } from 'react'
import { useGet } from '../../../customHooks/useGet'
import { useNavigate } from 'react-router-dom'
import InputFileUpload from './AddProductImage'
import ModeIcon from '@mui/icons-material/Mode'
import ContainerPage from '../../../components/ContainerPage'
import { formatNumber } from '../../../commons'
import InventoryIcon from '@mui/icons-material/Inventory'
import LocalOfferIcon from '@mui/icons-material/LocalOffer'
import { AppContext } from '../../../contexts/AppContext'

function ProductDetails() {
  const navigate = useNavigate()
  const { data, getData } = useGet()
  const { userInfo } = useContext(AppContext)
  const [productState, setProductState] = useState({})

  useEffect(() => {
    getData(`${process.env.REACT_APP_API}/api/products/${window.location.pathname.split('/')[2]}`)
  }, [])

  useEffect(() => {
    if (data.length > 0) {
      setProductState((prev) => ({ ...prev, ...data.flat(Infinity)[0], images: data.flat(Infinity)[0]?.image }))
    }
  }, [data])

  return (
    <ContainerPage title='Detalle de producto'>
      <Grid item xs={12}>
        <Card variant='outlined'>
          <CardHeader
            avatar={<LocalOfferIcon color='disabled' />}
            action={
              <>
                {userInfo?.role === 'administrator' && (
                  <IconButton onClick={() => navigate(`/product/${productState.product_code}/edit`)}>
                    <ModeIcon />
                  </IconButton>
                )}
              </>
            }
          />
          <CardContent>
            {data && (
              <Grid container spacing={1}>
                <Grid item xs={12} style={{ position: 'relative' }}>
                  <img
                    style={{
                      objectFit: 'cover',
                      objectPosition: 'center',
                      aspectRatio: '1 / 1',
                      width: ' 100%',
                      height: '100%',
                      borderRadius: '10px',
                    }}
                    src={
                      productState.images && typeof productState.images === 'string'
                        ? `${productState.image}`
                        : productState.images && URL.createObjectURL(productState.images)
                    }
                  ></img>
                </Grid>
                <Grid item xs={12}>
                  {userInfo?.role === 'administrator' && (
                    <InputFileUpload edit productState={productState} setProductState={setProductState} />
                  )}
                </Grid>
                <Grid item xs={7}>
                  <Grid item xs={12}>
                    <Typography fontWeight='600' fontSize='14px'>
                      {productState?.name}
                    </Typography>
                  </Grid>
                  <Grid item xs={12}>
                    <Typography fontSize='14px'>{productState?.product_code}</Typography>
                  </Grid>
                  <Grid item xs={12} mb={3}>
                    <Typography fontSize='14px'>
                      Disponible: {productState?.total} unidades
                    </Typography>
                  </Grid>
                </Grid>
                <Grid item xs={5} textAlign='center' alignSelf='center'>
                  <Typography variant='h5'> {formatNumber(productState?.price || 0)}</Typography>
                </Grid>
              </Grid>
            )}
          </CardContent>
        </Card>
      </Grid>
      {userInfo?.role === 'administrator' && (
      <Grid item xs={12}>
        <Card variant='outlined'>
          <CardContent>
            <CardHeader
              style={{ paddingLeft: '0px', display: 'flex', alignItems: 'center' }}
              avatar={<InventoryIcon color='disabled' />}
              title={<Typography>Detalle de inventario</Typography>}
              action={
             
                
                    <IconButton onClick={() => navigate(`/product/${productState.product_code}/add-inventory`)}>
                      +
                    </IconButton>
                
              }
            />
            <Grid container>
              {data.length > 0 &&
                productState?.lotes?.map((j, h) => (
                  <Fragment key={h}>
                    <Grid item xs={12} display='flex' justifyContent='space-between' pt={2}>
                      <Typography>Factura </Typography> <Typography> {j.bill_code} </Typography>
                      <Typography> {(j.createdAt && new Date(j.createdAt)?.toLocaleDateString()) || ''}</Typography>
                    </Grid>
                    <Grid item xs={12} display='flex' justifyContent='space-between'>
                      Unidades {j.quantity}
                    </Grid>
                    <Grid item xs={12} display='flex' justifyContent='space-between'>
                      Proveedor {j.provider_name}
                    </Grid>
                    <Grid item xs={12} display='flex' justifyContent='space-between'>
                      Nombre de producto en proveedor {j.product_name_by_provider}
                    </Grid>
                    <Grid item xs={12} pt={2}>
                      <Divider />
                    </Grid>
                  </Fragment>
                ))}
            </Grid>
          </CardContent>
        </Card>
      </Grid>)}
    </ContainerPage>
  )
}

export default ProductDetails
