import { useContext, useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { Button, Grid, Typography, Checkbox, FormControlLabel, FormGroup } from '@mui/material'
import CartList from '../../components/CartList/CartList'
import EmptyCart from './components/EmptyCart'
import { AppContext } from '../../contexts/AppContext'
import styles from './styles/cartScreenStyles'
import { usePost } from '../../customHooks/usePost'
import ContainerPage from '../../components/ContainerPage'
import { formatNumber } from '../../commons'

const CartScreen = () => {
  const navigate = useNavigate()

  const { totalPrice, cart, startOrder, setCartItems } = useContext(AppContext)
  const userInfo = {}
  const { postData, isLoading, data } = usePost()
  const [paymentMethod, setPaymentMethod] = useState('Efectivo')

  useEffect(() => {
    let timeOut
    if (data.length > 0) {
      timeOut = setTimeout(() => {
        setCartItems([])
        navigate(`/`)
      }, 1500)
    }

    return () => clearTimeout(timeOut)
  }, [data])

  const handleOrder = (e) => {
    e.preventDefault()
    if (cart.length < 1) {
      return
    }
    
    postData(
      `${process.env.REACT_APP_API}/api/transactions/payment`,
      JSON.stringify({
        cart,
        total: Number(totalPrice()),
        paymentMethod,
      })
    )
  }
  return (
    <div>
      {!cart.length > 0 ? (
        <Grid container justifyContent='center' direction='row' alignItems='center' p={1} style={{ marginTop: '15px' }}>
          <EmptyCart />
        </Grid>
      ) : (
        !startOrder && (
          <ContainerPage title='Resumen del pedido'>
            <Grid item xs={12} md={8}>
              <CartList />
              <Typography align='right'>
                Importe total <b style={{ fontSize: '18px' }}>{formatNumber(totalPrice())}</b>
              </Typography>
              <Grid item xs={12} mt={2} mb={2}>
                <FormGroup>
                  <FormControlLabel
                    labelPlacement='start'
                    onChange={() => setPaymentMethod('Mercadopago')}
                    checked={paymentMethod === 'Mercadopago'}
                    control={<Checkbox color='primary' />}
                    label='Mercadopago'
                  />
                  <FormControlLabel
                    labelPlacement='start'
                    onChange={() => setPaymentMethod('Efectivo')}
                    checked={paymentMethod === 'Efectivo'}
                    control={<Checkbox color='primary' />}
                    label='Efectivo'
                  />
                </FormGroup>
              </Grid>
            </Grid>
            {!userInfo ? (
              <Grid item xs={12} md={8} sx={styles.cartButtons}>
                <Button variant='outlined' color='secondary' onClick={() => navigate('/sign-in')}>
                  Logueate para continuar
                </Button>
              </Grid>
            ) : (
              <Grid item xs={12}>
                <Button variant='outlined' disabled={isLoading} fullWidth color='secondary' onClick={handleOrder}>
                  Confirmar
                </Button>
              </Grid>
            )}
          </ContainerPage>
        )
      )}
    </div>
  )
}

export default CartScreen
