import { Routes, Route } from 'react-router-dom'
import HomePage from '../pages/HomePage'
import ProductsPage from '../pages/ProductsPage'
import BillsPage from '../pages/BillsPage'
import UserPage from '../pages/UsersPage'
import ChangeUnitPrice from '../pages/ChangeUnitPrice'
import ConsigmentsPage from '../pages/ConsigmentsPage'
import ProvidersPage from '../pages/ProvidersPage'
import Layout from '../components/Layout/Layout'
import CartScreen from '../pages/CartScreen/CartScreen'
import DashboardPage from '../pages/DashboardPage'
import SellPage from '../pages/SellPage'
import ProductDetails from '../pages/ProductsPage/components/ProductDetails'
import AddProduct from '../pages/ProductsPage/components/AddProduct'
import AddBill from '../pages/BillsPage/components/AddBill'
import BillDetails from '../pages/BillsPage/components/BillDetails'
import AddExpense from '../pages/BillsPage/components/AddExpense'
import RegisterConsigment from '../pages/ConsigmentsPage/RegisterConsigments'
import EditProduct from '../pages/ProductsPage/components/EditProduct'
import SignIn from '../pages/SingIn'
import AuthRoutes from './authRoutes'
import PublicRoutes from './publicRoutes'
import AddProvider from '../pages/ProvidersPage/components/AddProvider'
import Addinventory from '../pages/ProductsPage/components/AddInventory'
import AddUser from '../pages/UsersPage/components/AddUser'
import EditUser from '../pages/UsersPage/components/EditUser'
import SellDetail from '../pages/SellPage/components/SellDetails'
import ProfilePage from '../pages/ProfilePage'
import PercentagePage from '../pages/PercentagePage'
import AddPercentage from '../pages/PercentagePage/components/AddPercentage'


const App = () => {

  return (
    <Routes>
      <Route path='/' element={<Layout />}>
        <Route
          path='/sign-in'
          element={
            <PublicRoutes>
              <SignIn />
            </PublicRoutes>
          }
        />
 


<Route
          path='/'
          element={
            <AuthRoutes>
              <HomePage />
            </AuthRoutes>
          }
        />
        <Route
          path='/products'
          element={
            <AuthRoutes>
              <ProductsPage />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/product/add'
          element={
            <AuthRoutes>
              <AddProduct />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/product/:id'
          element={
            <AuthRoutes>
              <ProductDetails />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/product/:id/edit'
          element={
            <AuthRoutes>
              <EditProduct />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/product/:id/add-inventory'
          element={
            <AuthRoutes>
              <Addinventory />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/bills'
          element={
            <AuthRoutes>
              <BillsPage />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/bills/add-bill'
          element={
            <AuthRoutes>
              <AddBill />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/bills/add-expense'
          element={
            <AuthRoutes>
              <AddExpense />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/bills/:id/:provider_id'
          element={
            <AuthRoutes>
              <BillDetails />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/users'
          element={
            <AuthRoutes>
              <UserPage />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/users/add'
          element={
            <AuthRoutes>
              <AddUser />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/users/:id'
          element={
            <AuthRoutes>
              <EditUser />{' '}
            </AuthRoutes>
          }
        />
        <Route path='/percentages' element={  <AuthRoutes><PercentagePage /></AuthRoutes>} />
        <Route path='/percentages/add' element={  <AuthRoutes><AddPercentage /></AuthRoutes>} />
        <Route
          path='/providers'
          element={
            <AuthRoutes>
              <ProvidersPage />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/providers/add'
          element={
            <AuthRoutes>
              <AddProvider />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/profile'
          element={
            <AuthRoutes>
              <ProfilePage />
            </AuthRoutes>
          }
        />
        <Route
          path='/consigments'
          element={
            <AuthRoutes>
              <ConsigmentsPage />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/consigments/add'
          element={
            <AuthRoutes>
              <RegisterConsigment />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/changeunitprice'
          element={
            <AuthRoutes>
              <ChangeUnitPrice />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/cart'
          element={
            <AuthRoutes>
              <CartScreen />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/dashboard'
          element={
            <AuthRoutes>
              <DashboardPage />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/sells'
          element={
            <AuthRoutes>
              <SellPage />{' '}
            </AuthRoutes>
          }
        />
        <Route
          path='/sells/:id'
          element={
            <AuthRoutes>
              <SellDetail />{' '}
            </AuthRoutes>
          }
        />


   
      </Route>
    </Routes>
  )
}

export default App
