import { useContext, useState } from 'react'
import Box from '@mui/material/Box'
import SwipeableDrawer from '@mui/material/SwipeableDrawer'
import List from '@mui/material/List'
import Divider from '@mui/material/Divider'
import ListItem from '@mui/material/ListItem'
import ListItemButton from '@mui/material/ListItemButton'
import ListItemIcon from '@mui/material/ListItemIcon'
import { ListItemText, IconButton } from '@mui/material'
import { useNavigate } from 'react-router-dom'
import MenuIcon from '@mui/icons-material/Menu'
import Badge from '@mui/material/Badge'
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart'
import { AppContext } from '../../contexts/AppContext'
import { SELLER_ROLE } from '../../cofig/role'
import { Login } from '@mui/icons-material'

function CustomBadge({ children }) {
  const { totalQuantity } = useContext(AppContext)
  return (
    <Badge
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left',
      }}
      badgeContent={totalQuantity()}
      color='primary'
    >
      {children}
    </Badge>
  )
}
export default function Menu() {
  const navigate = useNavigate()

  const { userInfo, setUserInfo } = useContext(AppContext)
  const [state, setState] = useState(false)

  const toggleDrawer = (anchor, open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return
    }

    setState(!state)
  }

  const list = () => (
    <Box
      sx={{ width: 'auto', color: 'black' }}
      role='presentation'
      onClick={toggleDrawer(false)}
      onKeyDown={toggleDrawer(false)}
    >
      {userInfo && (
        <>
          <List>
            <ListItem key={'Tienda'} onClick={() => navigate('/')} disablePadding>
              <ListItemButton>
                <ListItemIcon>{/* {index % 2 === 0 ? <InboxIcon /> : <MailIcon />} */}</ListItemIcon>
                <ListItemText primary={'Tienda'} />
              </ListItemButton>
            </ListItem>
            <ListItem key={'Ventas'} onClick={() => navigate('/sells')} disablePadding>
              <ListItemButton>
                <ListItemIcon>{/* {index % 2 === 0 ? <InboxIcon /> : <MailIcon />} */}</ListItemIcon>
                <ListItemText primary={'Ventas'} />
              </ListItemButton>
            </ListItem>
            <ListItem key={'Perfil'} onClick={() => navigate('/profile')} disablePadding>
              <ListItemButton>
                <ListItemIcon>{/* {index % 2 === 0 ? <InboxIcon /> : <MailIcon />} */}</ListItemIcon>
                <ListItemText primary={'Perfil'} />
              </ListItemButton>
            </ListItem>
            {userInfo?.role !== SELLER_ROLE && (
              <>
                <ListItem key={'Dashboard'} onClick={() => navigate('/dashboard')} disablePadding>
                  <ListItemButton>
                    <ListItemIcon>{/* {index % 2 === 0 ? <InboxIcon /> : <MailIcon />} */}</ListItemIcon>
                    <ListItemText primary={'Dashboard'} />
                  </ListItemButton>
                </ListItem>
                <ListItem key={'Consignaciones'} onClick={() => navigate('/consigments')} disablePadding>
                  <ListItemButton>
                    <ListItemIcon>{/* {index % 2 === 0 ? <InboxIcon /> : <MailIcon />} */}</ListItemIcon>
                    <ListItemText primary={'Consignaciones'} />
                  </ListItemButton>
                </ListItem>
                <ListItem key={'Porcentajes'} onClick={() => navigate('/percentages')} disablePadding>
                  <ListItemButton>
                    <ListItemIcon>{/* {index % 2 === 0 ? <InboxIcon /> : <MailIcon />} */}</ListItemIcon>
                    <ListItemText primary={'Porcentajes'} />
                  </ListItemButton>
                </ListItem>
                <ListItem key={'Productos'} onClick={() => navigate('/products')} disablePadding>
                  <ListItemButton>
                    <ListItemIcon>{/* {index % 2 === 0 ? <InboxIcon /> : <MailIcon />} */}</ListItemIcon>
                    <ListItemText primary={'Productos'} />
                  </ListItemButton>
                </ListItem>
                <ListItem key={'Facturas'} onClick={() => navigate('/bills')} disablePadding>
                  <ListItemButton>
                    <ListItemIcon>{/* {index % 2 === 0 ? <InboxIcon /> : <MailIcon />} */}</ListItemIcon>
                    <ListItemText primary={'Facturas'} />
                  </ListItemButton>
                </ListItem>
                <ListItem key={'Usuarios'} onClick={() => navigate('/users')} disablePadding>
                  <ListItemButton>
                    <ListItemIcon>{/* {index % 2 === 0 ? <InboxIcon /> : <MailIcon />} */}</ListItemIcon>
                    <ListItemText primary={'Usuarios'} />
                  </ListItemButton>
                </ListItem>
                <ListItem key={'Proveedores'} onClick={() => navigate('/providers')} disablePadding>
                  <ListItemButton>
                    <ListItemIcon>{/* {index % 2 === 0 ? <InboxIcon /> : <MailIcon />} */}</ListItemIcon>
                    <ListItemText primary={'Proveedores'} />
                  </ListItemButton>
                </ListItem>
              </>
            )}
          </List>

          <Divider />
          <List>
            {['Cerrar Sesion'].map((text, index) => (
              <ListItem
                key={text}
                disablePadding
                onClick={() => {
                  localStorage.removeItem('setFMI')
                  setUserInfo()
                }}
              >
                <ListItemButton>
                  <ListItemIcon>{/* {index % 2 === 0 ? <InboxIcon /> : <MailIcon />} */}</ListItemIcon>
                  <ListItemText primary={text} />
                </ListItemButton>
              </ListItem>
            ))}
          </List>
        </>
      )}
      {!userInfo && (
        <ListItem key={'Ingresar'} onClick={() => navigate('/sign-in')} disablePadding>
          <ListItemButton>
            <ListItemIcon sx={{ minWidth: { xs: '35px', md: '45px' } }}>
              <Login />
            </ListItemIcon>
            <ListItemText primary='Ingresar' />
          </ListItemButton>
        </ListItem>
      )}
    </Box>
  )

  return (
    <>
      <CustomBadge>
        <IconButton onClick={() => navigate('/cart')}>
          <ShoppingCartIcon style={{ color: 'white' }} />
        </IconButton>
      </CustomBadge>
      <IconButton onClick={toggleDrawer(true)}>
        <MenuIcon style={{ color: 'white' }} />{' '}
      </IconButton>

      <SwipeableDrawer anchor={'bottom'} open={state} onClose={toggleDrawer(false)} onOpen={toggleDrawer(true)}>
        {list()}
      </SwipeableDrawer>
    </>
  )
}
