import { Button, Card, CardContent, CardHeader, Grid } from '@mui/material'
import { Fragment, useEffect, useState } from 'react'
import ShoppingBagIcon from '@mui/icons-material/ShoppingBag'
import { useGet } from '../../customHooks/useGet'
import SellIcon from '@mui/icons-material/Sell'
import PointOfSaleIcon from '@mui/icons-material/PointOfSale'
import PriceChangeIcon from '@mui/icons-material/PriceChange'
import PaidIcon from '@mui/icons-material/Paid'
import PendingActionsIcon from '@mui/icons-material/PendingActions'
import ContainerPage from '../../components/ContainerPage'
import { formatNumber } from '../../commons'
import DashboardTransactions from './components/DashboardTransactions'
import TimePickers from '../../components/TimePikers'
import { endOfDay, startOfDay } from 'date-fns'

function DashboardPage() {
  const [useFilter, setUseFilter] = useState(false)
  const [startDate, setStartDate] = useState(new Date())
  const [endDate, setEndDate] = useState(new Date())

  const { data, error, getData, isLoading, setData } = useGet()

  useEffect(() => {
    getData(`${process.env.REACT_APP_API}/api/dashboard`)
  }, [])

  if (isLoading) {
    return <p>Loading...</p>
  }

  if (error) {
    return <p>Error: {error.message}</p>
  }

  const handleData = (startDate, endDate) => {
    setData(() => [])
    getData(
      `${process.env.REACT_APP_API}/api/dashboard?start-date=${startOfDay(new Date(startDate))}&end-date=${endOfDay(
        new Date(endDate)
      )}`
    )
  }

  const handleNumberValue = (number)=>{
    return number? number : 0
  }
  return (
    <ContainerPage
      title={'Dashboard'}
      actions={
        <Button
          onClick={() => {
            if (useFilter) {
              setData(() => [])
              getData(`${process.env.REACT_APP_API}/api/dashboard`)
            }
            setUseFilter(!useFilter)
          }}
        >
          filtrar
        </Button>
      }
    >
      {useFilter && (
        <TimePickers
          actions={handleData}
          startDate={startDate}
          endDate={endDate}
          setEndDate={setEndDate}
          setStartDate={setStartDate}
        />
      )}
      {isLoading
        ? 'Cargando'
        : data.length > 0 &&
          data.flat(Infinity).map((i, j) => (
            <Fragment key={j}>
              <Grid item xs={6}>
                <Card>
                  <CardHeader
                    avatar={<ShoppingBagIcon color='disabled' />}
                    title={<p style={{ fontSize: '14px' }}>Compras</p>}
                  />
                  <CardContent style={{ textAlign: 'center', marginTop: '-1rem', fontSize: '20px' }}>
                    {formatNumber(i?.buy || 0)}
                  </CardContent>
                </Card>
              </Grid>
              <Grid item xs={6}>
                <Card>
                  <CardHeader
                    avatar={<SellIcon color='disabled' />}
                    title={<p style={{ fontSize: '14px' }}>Ventas</p>}
                  />
                  <CardContent style={{ textAlign: 'center', marginTop: '-1rem', fontSize: '20px' }}>
                    {formatNumber(i?.total || 0)}
                  </CardContent>
                </Card>
              </Grid>
              <Grid item xs={6}>
                <Card>
                  <CardHeader
                    avatar={<PaidIcon color='disabled' />}
                    title={<p style={{ fontSize: '14px' }}>Comisiones</p>}
                  />
                  <CardContent style={{ textAlign: 'center', marginTop: '-1rem', fontSize: '20px' }}>
                    {formatNumber(i?.comissionAmount || 0)}
                  </CardContent>
                </Card>
              </Grid>
              <Grid item xs={6}>
                <Card>
                  <CardHeader
                    avatar={<PriceChangeIcon color='disabled' />}
                    title={<p style={{ fontSize: '14px' }}>Costo/venta</p>}
                  />
                  <CardContent style={{ textAlign: 'center', marginTop: '-1rem', fontSize: '20px' }}>
                    {formatNumber(i?.cost_of_sale || 0)}
                  </CardContent>
                </Card>
              </Grid>
              <Grid item xs={6}>
                <Card>
                  <CardHeader
                    avatar={<PointOfSaleIcon color='disabled' />}
                    title={<p style={{ fontSize: '14px' }}>Caja</p>}
                  />
                  <CardContent style={{ textAlign: 'center', marginTop: '-1rem', fontSize: '20px' }}>
                    {formatNumber(-i?.buy + i?.total - handleNumberValue(i?.comissionAmount) - handleNumberValue(i?.totalExpenseAmount))}
                  </CardContent>
                </Card>
              </Grid>
              <Grid item xs={6}>
                <Card>
                  <CardHeader
                    avatar={<PendingActionsIcon color='disabled' />}
                    title={<p style={{ fontSize: '14px' }}>Comis. s/p</p>}
                  />
                  <CardContent style={{ textAlign: 'center', marginTop: '-1rem', fontSize: '20px' }}>
                    {formatNumber(i?.totalComissionNotLiquidated || 0)}
                  </CardContent>
                </Card>
              </Grid>
              <Grid item xs={6}>
                <Card>
                  <CardHeader
                    avatar={<PendingActionsIcon color='disabled' />}
                    title={<p style={{ fontSize: '14px' }}>Otros gastos</p>}
                  />
                  <CardContent style={{ textAlign: 'center', marginTop: '-1rem', fontSize: '20px' }}>
                    {formatNumber(i?.totalExpenseAmount || 0)}
                  </CardContent>
                </Card>
              </Grid>{' '}
              <Grid item xs={6}>
                <Card>
                  <CardHeader
                    avatar={<PendingActionsIcon color='disabled' />}
                    title={<p style={{ fontSize: '14px' }}>Ganancia</p>}
                  />
                  <CardContent style={{ textAlign: 'center', marginTop: '-1rem', fontSize: '20px' }}>
                    {formatNumber(i.total - i.cost_of_sale - i.comissionAmount)}
                  </CardContent>
                </Card>
              </Grid>
              <DashboardTransactions data={data} />
            </Fragment>
          ))}
    </ContainerPage>
  )
}

export default DashboardPage
