import { Button, Card, CardContent, CardHeader, Divider, Grid, Typography } from '@mui/material'
import { Fragment, useContext, useEffect, useState } from 'react'
import { useGet } from '../../customHooks/useGet'
import ContainerPage from '../../components/ContainerPage'
import { formatNumber } from '../../commons'
import CompareArrowsIcon from '@mui/icons-material/CompareArrows'
import { AppContext } from '../../contexts/AppContext'
import { useNavigate } from 'react-router-dom'
import TimePickers from '../../components/TimePikers'
import { endOfDay, startOfDay } from 'date-fns'

function SellPage() {
  const { userInfo } = useContext(AppContext)
  const [useFilter, setUseFilter] = useState(false)
  const [startDate, setStartDate] = useState(new Date())
  const [endDate, setEndDate] = useState(new Date())
  const navigate = useNavigate()
  const { data, getData, setData } = useGet()

  useEffect(() => {
    if (userInfo) {
      getData(`${process.env.REACT_APP_API}/api/users/${userInfo._id}/sells`)
    }
  }, [userInfo])

  const handleData = (startDate, endDate) => {
    setData(() => [])
    getData(
      `${process.env.REACT_APP_API}/api/users/${userInfo._id}/sells?start-date=${startOfDay(
        new Date(startDate)
      )}&end-date=${endOfDay(new Date(endDate))}`
    )
  }
  return (
    <ContainerPage
      title='Ventas'
      actions={
        <Button
          onClick={() => {
            if (useFilter) {
              setData(() => [])
              getData(`${process.env.REACT_APP_API}/api/users/${userInfo._id}/sells`)
            }
            setUseFilter(!useFilter)
          }}
        >
          filtrar
        </Button>
      }
    >
      {useFilter && (
        <TimePickers
          actions={handleData}
          startDate={startDate}
          endDate={endDate}
          setEndDate={setEndDate}
          setStartDate={setStartDate}
        />
      )}
      <Grid item xs={6} mt={2}>
        <Card>
          <CardHeader title={<p style={{ fontSize: '14px' }}>Total</p>} />
          <CardContent>{formatNumber(data[0]?.transactionsTotalized[0]?.total || 0)}</CardContent>
        </Card>
      </Grid>
      <Grid item xs={6} mt={2}>
        <Card>
          <CardHeader title={<p style={{ fontSize: '14px' }}>Comisiones cobradas</p>} />
          <CardContent>{formatNumber(data[0]?.transactionsTotalized[0]?.totalComissionLiquidated || 0)}</CardContent>
        </Card>
      </Grid>
      <Grid item xs={6}>
        <Card>
          <CardHeader title={<p style={{ fontSize: '14px' }}>Items vendidos</p>} />
          <CardContent>{data[0]?.transactionsTotalized[0]?.transactions || 0}</CardContent>
        </Card>
      </Grid>
      <Grid item xs={6}>
        <Card>
          <CardHeader title={<p style={{ fontSize: '14px' }}>Comisiones p/cobrar</p>} />
          <CardContent>{formatNumber(data[0]?.transactionsTotalized[0]?.totalComissionNotLiquidated || 0)}</CardContent>
        </Card>
      </Grid>
      <Grid item xs={12}>
        <Card>
          <CardHeader avatar={<CompareArrowsIcon color='disabled' />} title={<Typography>Transacciones</Typography>} />
          <CardContent>
            <Grid container>
              <Grid p={1} item xs={3} fontSize='14px' fontWeight={600}>
                Fecha
              </Grid>
              <Grid p={1} item xs={3} fontSize='14px' fontWeight={600}>
                Monto
              </Grid>
              <Grid p={1} item xs={3} fontSize='14px' fontWeight={600}>
                Comision
              </Grid>
              <Grid p={1} textAlign='end' item xs={3} fontSize='14px' fontWeight={600}>
                C/paga
              </Grid>
              <Grid item xs={12}>
                <Divider variant='fullWidth' style={{ marginTop: '-5px' }} />
              </Grid>
              {data[0]?.transactions?.map((i) => (
                <Fragment key={i._id}>
                  <Grid item p={1} xs={3} fontSize='14px'>
                    {new Date(i.createdAt).toLocaleString()}
                  </Grid>

                  <Grid item p={1} xs={3} fontSize='14px'>
                    {formatNumber(i.total)}
                  </Grid>
                  <Grid item p={1} xs={3} fontSize='14px'>
                    {formatNumber(i.comissionAmount)}
                  </Grid>
                  <Grid item p={1} xs={3} textAlign='end' fontSize='14px' onClick={() => navigate(`/sells/${i._id}`)}>
                    {i.totalComissionNotLiquidated === 0 ? 'Si' : 'No'}
                  </Grid>
                  <Grid item xs={12}>
                    <Divider variant='fullWidth' />
                  </Grid>
                </Fragment>
              ))}
            </Grid>
          </CardContent>
        </Card>
      </Grid>
      <Grid item xs={12}>
        <Divider variant='fullWidth' />
      </Grid>
    </ContainerPage>
  )
}

export default SellPage
