import { Card, CardContent, CardHeader, Grid, IconButton } from '@mui/material'
import { useEffect } from 'react'
import ContainerPage from '../../components/ContainerPage'
import { useGet } from '../../customHooks/useGet'
import BusinessIcon from '@mui/icons-material/Business'
import { useNavigate } from 'react-router-dom'

function ProvidersPage() {
  const navigate = useNavigate()

  const { data, error, getData, isLoading } = useGet()

  useEffect(() => {
    async function fetch() {
      await getData(`${process.env.REACT_APP_API}/api/providers`)
    }
    fetch()
  }, [])

  if (isLoading) {
    return <p>Loading...</p>
  }

  if (error) {
    return <p>Error: {error.message}</p>
  }

  return (
    <ContainerPage
      title={'Proveedores'}
      actions={<IconButton onClick={() => navigate('/providers/add')}>+</IconButton>}
    >
      {isLoading ? (
        'Cargando'
      ) : (
        <>
          <Grid item xs={12}>
            <Card>
              <CardHeader avatar={<BusinessIcon color='disabled'/>} />
              <CardContent>
                <Grid container>
                  {data.flat(Infinity).map((i, j) => (
                    <Grid item xs={12} key={i._id}>
                      {j} {i.name}
                    </Grid>
                  ))}
                </Grid>
              </CardContent>
            </Card>
          </Grid>
          {error && 'Error'}
        </>
      )}
    </ContainerPage>
  )
}

export default ProvidersPage
