import { useContext, useEffect, useState } from 'react'
import {
  Grid,
  TextField,
  Button,
  Card,
  CardContent,
  CardActions,
  CardHeader,
  FormHelperText,
  Typography,
} from '@mui/material'
import { AppContext } from '../../contexts/AppContext'
import ContainerPage from '../../components/ContainerPage'
import LoginIcon from '@mui/icons-material/Login'
import { usePost } from '../../customHooks/usePost'
import parseJwt from '../../middlewares'

const initialState = {
  email: '',
  password: '',
}

const SignIn = () => {
  const styles = {}
  const { setUserInfo } = useContext(AppContext)

  const { data, error, isLoading, postData } = usePost()

  const [signInState, setSignIngState] = useState(initialState)

  useEffect(() => {
    if (data.length > 0) {
      const decoded = parseJwt(data[0].token)

      const userSession = {
        ...decoded,
        token: data[0].token,
      }

      localStorage.setItem('setFMI', JSON.stringify(data[0].token))

      setUserInfo(userSession)
    }
  }, [data])

  const handleSubmit = (e) => {
    e.preventDefault()
    postData(`${process.env.REACT_APP_API}/api/users/auth`, JSON.stringify(signInState))
  }

  return (
    <ContainerPage title='' backAction={false}>
      <Grid item xs={12} component={'form'} autoComplete='off' onSubmit={handleSubmit}>
        <Card variant='outlined'>
          <CardHeader
            sx={styles.loginCardHeader}
            avatar={<LoginIcon color='disabled' />}
            title={<b>Hypnotic Grow Shop</b>}
            subheader='Iniciar sesion'
          />
          <CardContent>
            <Grid container justifyContent='center' alignItems='center' rowSpacing={2}>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label='Email'
                  required={true}
                  variant='outlined'
                  type='email'
                  value={signInState.email}
                  onChange={(e) => setSignIngState({ ...signInState, email: e.target.value })}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label='Contraseña'
                  required={true}
                  variant='outlined'
                  type='password'
                  autoComplete='false'
                  value={signInState.password}
                  onChange={(e) => setSignIngState({ ...signInState, password: e.target.value })}
                />
                <FormHelperText style={{ textAlign: 'end' }}>
                  Olvide mi <b style={{ cursor: 'pointer' }}>contraseña</b>
                </FormHelperText>
              </Grid>
              <Grid item xs={12}>
                {error && <Typography color='red'>Error: {error.message}</Typography>}
              </Grid>
            </Grid>
          </CardContent>
          <CardActions>
            <Button type='submit' fullWidth variant='contained' color='secondary'>
              {isLoading ? 'Ingresando...' : data.length > 0 ? 'Ingreso' : 'Ingresar'}
            </Button>
          </CardActions>
        </Card>
      </Grid>
    </ContainerPage>
  )
}

export default SignIn
