import { Avatar, Card, CardContent, CardHeader, Grid, Typography } from '@mui/material'
import { useEffect } from 'react'
import { useGet } from '../../../customHooks/useGet'
import ContainerPage from '../../../components/ContainerPage'
import ContentPasteIcon from '@mui/icons-material/ContentPaste'
import { formatNumber } from '../../../commons'

function BillDetails() {
  const { data, getData } = useGet()

  useEffect(() => {
    getData(`${process.env.REACT_APP_API}/api/bills/${window.location.pathname.split('/')[2]}/${window.location.pathname.split('/')[3]}`)
  }, [])

  return (
    <ContainerPage title={'Detalle de factura'}>
      <Grid item xs={12}>
        <Card>
          <CardHeader
            avatar={<ContentPasteIcon color='disabled' />}
            title={<Typography>{data.flat(Infinity)[0]?.bill_code}</Typography>}
            action={`${
              isNaN(
                data
                  .flat(Infinity)[0]
                  ?.details?.map((j) => j?.quantity | 0)
                  .reduce((a, b) => a + b, 0)
              )
                ? 0
                : data
                    .flat(Infinity)[0]
                    ?.details?.map((j) => j?.quantity | 0)
                    .reduce((a, b) => a + b, 0)
            } u`}
          />
          <CardContent>
            <Grid container spacing={1}>
              <Grid item xs={4} textAlign='center'>
                <Typography variant='body1'>Fecha</Typography>
                <Typography variant='body2'>
                  {new Date(data.flat(Infinity)[0]?.bill_date).toLocaleDateString()}
                </Typography>
              </Grid>
              <Grid item xs={4} textAlign='center'>
                <Typography variant='body1'>Proveedor</Typography>
                <Typography variant='body2'> {data.flat(Infinity)[0]?.provider_name}</Typography>
              </Grid>
              <Grid item xs={4} textAlign='center'>
                <Typography variant='body1'>Monto</Typography>
                <Typography variant='body2'> {formatNumber(data.flat(Infinity)[0]?.total || 0)}</Typography>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Grid>
      {data.flat(Infinity)[0] &&
        Object.values(data.flat(Infinity)[0]?.details).length > 0 &&
        data
          .flat(Infinity)[0]
          ?.details?.flat(Infinity)
          .map((j, h) => (
            <Grid item xs={12} key={h}>
              <Card>
                <CardHeader
                  avatar={<Avatar src={`${j?.image}`} />}
                  title={j?.product_name}
                  subheader={j?.product_code}
                  action={<p> {`${j?.quantity} u`}</p>}
                />
                <CardContent>
                  <p style={{ fontSize: '14px' }}>{j?.provider_product_name}</p>
                  <p>Precio unitario {formatNumber(j?.unit_price)}</p>
                </CardContent>
              </Card>
            </Grid>
          ))}
    </ContainerPage>
  )
}

export default BillDetails
