import { Card, CardContent, CardHeader, Divider, Grid, Typography } from '@mui/material'
import { Fragment } from 'react'
import { formatNumber } from '../../../commons'
import CompareArrowsIcon from '@mui/icons-material/CompareArrows'
import { useNavigate } from 'react-router-dom'



function DashboardTransactions({ data }) {
  const navigate = useNavigate()
  return (
    <>
      <Grid item xs={12}>
        <Card>
          <CardHeader avatar={<CompareArrowsIcon color='disabled' />} title={<Typography>Transacciones</Typography>} />
          <CardContent>
            <Grid container>
              <Grid p={1} item xs={3} fontSize='14px' fontWeight={600}>
                Fecha
              </Grid>
              <Grid p={1} item xs={3} fontSize='14px' fontWeight={600}>
                Monto
              </Grid>
              <Grid p={1} item xs={3} fontSize='14px' fontWeight={600}>
                Comision
              </Grid>
              <Grid p={1} textAlign='end' item xs={3} fontSize='14px' fontWeight={600}>
                C/paga
              </Grid>
              <Grid item xs={12}>
                <Divider variant='fullWidth' style={{ marginTop: '-5px' }} />
              </Grid>
              {data[0]?.all_transactions?.map((i) => (
                <Fragment key={i._id}>
                  <Grid item color={i.type !== 'sell' ? 'red' : 'green'} p={1} xs={3} fontSize='14px'>
                 
                     {new Date(i.createdAt).toLocaleString()} 
                  </Grid>

                  <Grid item color={i.type !== 'sell' ? 'red' : 'green'} p={1} xs={3} fontSize='14px'>
                    {formatNumber(i.total)}
                  </Grid>
                  <Grid item color={i.type !== 'sell' ? 'red' : 'green'} p={1} xs={3} fontSize='14px'>
                    {formatNumber(i.comissionAmount)}
                  </Grid>
                  <Grid
                    item
                    onClick={() => navigate(`/sells/${i._id}`)}
                    color={i.type !== 'sell' ? 'red' : 'green'}
                    p={1}
                    xs={3}
                    textAlign='end'
                    fontSize='14px'
                  >
                    {i.totalComissionNotLiquidated === 0 ? 'Si' : 'No'}
                  </Grid>
                  <Grid item xs={12}>
                    <Divider variant='fullWidth' />
                  </Grid>
                </Fragment>
              ))}
            </Grid>
          </CardContent>
        </Card>
      </Grid>
      <Grid item xs={12}>
        <Divider variant='fullWidth' />
      </Grid>
    </>
  )
}

export default DashboardTransactions
